body{
    font-family: sans-serif;
}
.App{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
}
.coin_row{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}
.crypto_coins{
    margin: 2rem;
    text-align: center;
}
img{
    width: 50px;
    height: 50px;
}
h3{
    font-weight: 500;
    font-size: 1.05rem;
    color: rgba(0, 0, 0, 0.808);
}
p{
    font-size: 1rem;
    font-family: monospace, sans-serif;
    color: black;
}